"use client";

import React, { useMemo, useState } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Modal } from "@/components/ui/modal";
import { ChevronLeft, ChevronRight, Plus, Trash2, Send } from "lucide-react";

type RowStatus = "WORKING" | "LEAVE" | "HOLIDAY";
type TimesheetStatus = "DRAFT" | "SUBMITTED" | "APPROVED" | "REJECTED";

interface ProjectRow {
    id: number;
    project: string;
    status: RowStatus;
    hours: number;
    overtime: number;
    notes: string;
}

interface DayEntry {
    rows: ProjectRow[];
}

const PROJECTS = ["None", "Project Alpha", "Project Beta", "Internal"];

const STATUS_COLOR: Record<RowStatus, string> = {
    WORKING: "bg-blue-100 text-blue-700",
    LEAVE: "bg-orange-100 text-orange-700",
    HOLIDAY: "bg-green-100 text-green-700",
};

const EMPTY_ROW = (): ProjectRow => ({
    id: Date.now() + Math.random(),
    project: "None",
    status: "WORKING",
    hours: 8,
    overtime: 0,
    notes: "",
});

const startOfWeek = (d: Date) => {
    const date = new Date(d);
    date.setDate(date.getDate() - date.getDay());
    date.setHours(0, 0, 0, 0);
    return date;
};

const addDays = (d: Date, days: number) => {
    const date = new Date(d);
    date.setDate(date.getDate() + days);
    return date;
};

export default function WeeklyTimesheet() {
    const todayWeek = startOfWeek(new Date());
    const [currentWeek, setCurrentWeek] = useState(todayWeek);
    const [entries, setEntries] = useState<Record<string, DayEntry>>({});
    const [selectedDate, setSelectedDate] = useState<string | null>(null);
    const [status, setStatus] = useState<TimesheetStatus>("DRAFT");

    const isEditable = status === "DRAFT";

    const weekDays = useMemo(
        () =>
            Array.from({ length: 7 }).map((_, i) => {
                const date = addDays(currentWeek, i);
                return {
                    key: date.toISOString().split("T")[0],
                    date,
                };
            }),
        [currentWeek]
    );

    const openDay = (key: string) => {
        if (!isEditable) return;
        setEntries(p => ({
            ...p,
            [key]: p[key] ?? { rows: [EMPTY_ROW()] },
        }));
        setSelectedDate(key);
    };

    const updateRow = (id: number, patch: Partial<ProjectRow>) => {
        if (!selectedDate) return;
        setEntries(p => ({
            ...p,
            [selectedDate]: {
                rows: p[selectedDate].rows.map(r =>
                    r.id === id ? { ...r, ...patch } : r
                ),
            },
        }));
    };

    const addRow = () => {
        if (!selectedDate) return;
        setEntries(p => ({
            ...p,
            [selectedDate]: {
                rows: [...p[selectedDate].rows, EMPTY_ROW()],
            },
        }));
    };

    const removeRow = (id: number) => {
        if (!selectedDate) return;
        setEntries(p => ({
            ...p,
            [selectedDate]: {
                rows: p[selectedDate].rows.filter(r => r.id !== id),
            },
        }));
    };

    const submitTimesheet = () => setStatus("SUBMITTED");

    const canGoNext = currentWeek < todayWeek;

    return (
        <div className="p-6 space-y-6">
            <PageBreadcrumb pageTitle="Timesheet" />

            {/* Header */}
            <div className="flex justify-between items-center">
                <div>
                    <h2 className="text-lg font-semibold">
                        {currentWeek.toDateString()} – {addDays(currentWeek, 6).toDateString()}
                    </h2>
                    <span
                        className={`inline-block mt-1 px-3 py-1 rounded-full text-sm font-medium
              ${status === "DRAFT"
                                ? "bg-gray-200 text-gray-700"
                                : status === "SUBMITTED"
                                    ? "bg-blue-100 text-blue-700"
                                    : status === "APPROVED"
                                        ? "bg-green-100 text-green-700"
                                        : "bg-red-100 text-red-700"
                            }`}
                    >
                        {status}
                    </span>
                </div>

                {isEditable && (
                    <button
                        onClick={submitTimesheet}
                        className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
                    >
                        <Send size={16} /> Submit Timesheet
                    </button>
                )}
            </div>

            {/* Week Navigation */}
            <div className="flex items-center gap-4">
                <button onClick={() => setCurrentWeek(addDays(currentWeek, -7))}>
                    <ChevronLeft />
                </button>
                <button disabled={!canGoNext} onClick={() => setCurrentWeek(addDays(currentWeek, 7))}>
                    <ChevronRight />
                </button>
            </div>

            {/* Calendar */}
            <div className="grid grid-cols-7 gap-4">
                {weekDays.map(d => {
                    const day = entries[d.key];
                    return (
                        <div
                            key={d.key}
                            onClick={() => openDay(d.key)}
                            className={`rounded-xl border p-4 text-center cursor-pointer hover:shadow ${!isEditable && "opacity-60 cursor-not-allowed"
                                }`}
                        >
                            <div className="text-sm">{d.date.toLocaleDateString("default", { weekday: "short" })}</div>
                            <div className="text-xl font-bold">{d.date.getDate()}</div>

                            {day?.rows.map(r => (
                                <span
                                    key={r.id}
                                    className={`block mt-1 text-xs rounded px-2 py-0.5 ${STATUS_COLOR[r.status]}`}
                                >
                                    {r.project} – {r.status}
                                </span>
                            ))}
                        </div>
                    );
                })}
            </div>

            {/* Modal */}
            <Modal isOpen={!!selectedDate} onClose={() => setSelectedDate(null)} className="max-w-3xl p-6">
                {selectedDate && (
                    <div className="space-y-4">
                        <h2 className="font-semibold">{selectedDate}</h2>

                        {entries[selectedDate]?.rows.map(r => (
                            <div key={r.id} className="grid grid-cols-12 gap-2 items-center">
                                <select
                                    className="col-span-3 border rounded px-2 py-1"
                                    value={r.project}
                                    onChange={e => updateRow(r.id, { project: e.target.value })}
                                >
                                    {PROJECTS.map(p => (
                                        <option key={p}>{p}</option>
                                    ))}
                                </select>

                                <select
                                    className="col-span-2 border rounded px-2 py-1"
                                    value={r.status}
                                    onChange={e =>
                                        updateRow(r.id, { status: e.target.value as RowStatus })
                                    }
                                >
                                    <option value="WORKING">Working</option>
                                    <option value="LEAVE">Leave</option>
                                    <option value="HOLIDAY">Holiday</option>
                                </select>

                                <input
                                    type="number"
                                    disabled={r.status !== "WORKING"}
                                    value={r.hours}
                                    onChange={e => updateRow(r.id, { hours: Number(e.target.value) })}
                                    className="col-span-2 border rounded px-2 py-1"
                                />

                                <input
                                    type="number"
                                    disabled={r.status !== "WORKING"}
                                    value={r.overtime}
                                    onChange={e => updateRow(r.id, { overtime: Number(e.target.value) })}
                                    className="col-span-2 border rounded px-2 py-1"
                                />

                                <input
                                    value={r.notes}
                                    onChange={e => updateRow(r.id, { notes: e.target.value })}
                                    className="col-span-2 border rounded px-2 py-1"
                                    placeholder="Notes"
                                />

                                <Trash2 onClick={() => removeRow(r.id)} className="text-red-500 cursor-pointer" />
                            </div>
                        ))}

                        <button onClick={addRow} className="flex gap-2 text-blue-600 text-sm">
                            <Plus size={16} /> Add Project
                        </button>
                    </div>
                )}
            </Modal>
        </div>
    );
}
